import { Injectable } from '@angular/core';
import * as moment from 'moment';
import { HttpClient } from '@angular/common/http';


@Injectable({
    providedIn: 'root'
})
export class Auth {

    constructor(private http: HttpClient) { }

    async login(email: string, password: string) {
        try {
            this.http.post('/api/users/login', { email: email, password: password })
                .subscribe(res => {

                    this.setSession(res)
                }
                )

        }
        catch (err) { console.log("Yanlış Parola ya da Email") }


    }

    async register(username: String, firstname: String, lastname: String, email: String, password: String) {
        try {
            this.http.post('/api/users/register', { user_name: username, first_name: firstname,last_name: lastname ,email: email, password: password })
                .subscribe(res => {

                    this.setSession(res)
                    console.log("Kayıt Başarılı")
                }
                )

        }
        catch (err) { console.log("Yanlış Parola ya da Email") }


    }

    private setSession(authResult) {
        const expiresAt = moment().add(authResult.expiresIn, 'hours')

        localStorage.setItem('username', authResult.data.username)
        localStorage.setItem('id_token', authResult.token)
        localStorage.setItem("expires_at", JSON.stringify(expiresAt.valueOf()))

        if (authResult.token) console.log("Login Successful")
        else { console.log("Login Not Successful") }
    }

    logout() {
        localStorage.removeItem("username")
        localStorage.removeItem("id_token")
        localStorage.removeItem("expires_at")
    }

    isLoggedIn() {
        console.log("moment: " + moment().calendar())
        console.log("exp: " + this.getExpiration().calendar())
        console.log("islogged: " + moment().isBefore(this.getExpiration()))
        return moment().isBefore(this.getExpiration())

    }

    isLoggedOut() {
        return !this.isLoggedIn();
    }

    getExpiration() {
        const expiration = localStorage.getItem("expires_at");
        const expiresAt = JSON.parse(expiration);

        return moment(expiresAt);
    }

}
