import { Component, OnInit } from '@angular/core';
import { Auth } from '../../services/auth'
import { Router } from '@angular/router'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  

  constructor(private auth:Auth,private route: Router) { }

  ngOnInit(): void {
  }

  register(username: String,firstname: String,lastname: String , email: String,password: String){
    
      this.auth.register(username,firstname,lastname,email,password).then(() => {
        this.route.navigateByUrl('/')
        }).catch((err)=>{
          {
           console.log(err)
          }
        })
  }

}
