import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { JobadListComponent } from './components/jobad-list/jobad-list.component';

import {HttpClientModule} from '@angular/common/http';
import { JobadService } from './services/jobad.service';
import { Auth} from './services/auth';
import { AuthInterceptor} from './services/auth-interceptor';
import { Router, RouterModule, Routes } from '@angular/router';
import { HeaderComponent } from './components/header/header.component';
import { SearchComponent } from './components/search/search.component';
import { JobadDetailsComponent } from './components/jobad-details/jobad-details.component';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';


const routes: Routes = [
  {path:'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'category/:name/jobads/:id', component:JobadDetailsComponent},
  {path: 'category/jobads/:id', component:JobadDetailsComponent},
  {path: 'jobs/jobads/:id', component:JobadDetailsComponent},
  {path: 'jobads/:id', component:JobadDetailsComponent},
  {path: 'search/:keyword', component: JobadListComponent},
  {path: 'category/:name', component: JobadListComponent},
  {path: 'category', component: JobadListComponent},
  {path: 'jobs', component: JobadListComponent},
  {path: '', redirectTo:'/jobs', pathMatch:'full'},
  {path:'**', redirectTo:'/jobs',pathMatch:'full'}
]


@NgModule({
  declarations: [
    AppComponent,
    JobadListComponent,
    HeaderComponent,
    SearchComponent,
    JobadDetailsComponent,
    LoginComponent,
    RegisterComponent
  ],
  imports: [
    RouterModule.forRoot(routes),
    BrowserModule,
    HttpClientModule,
    NgbModule
  ],
  providers: [JobadService,Auth,AuthInterceptor],
  bootstrap: [AppComponent]
})
export class AppModule { }
